import React,{useState} from "react";

function SearchMovies() {

const [query, setQuery] = useState("")
const [movies, setMovies] = useState([]);

const handleChange = (e)=>{
    setQuery(e.target.value)
}


  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = `https://api.themoviedb.org/3/search/movie?api_key=b4bbafcd8eb0413f91be172da730a0db&language=en-US&query=${query}&page=1`;
    try{
        const result = await fetch(url)
        const data = await result.json()
setMovies(data.results)
    } catch(err){
        console.error(err)
    }
   
  };

  return (
    <form className="form" onSubmit={handleSubmit}>
      <label htmlFor="query" className="label">
        Movie Name
      </label>
      <input className="input" type="text" name="query" placeholder="James Bond" onChange={handleChange} value={query}/>
      <button className="button" type="submit">
        Submit
      </button>
    </form>
  );
}

export default SearchMovies;
